import {config} from '../components/config';

export const reducer = (state,action) => {
    switch(action.type) {
        case 'SWAP' :
            return ({
                ...state,
                rotate: !state.rotate,
                buying: state.selling,
                selling: state.buying
            }) 
        case 'UPDATE_SELLING' : 
            return ({
                ...state,
                selling: {flag: action.currency.slice(0,2) , currencyCode: action.currency, currency: config[action.currency].name},
                toggleLeft: !state.toggleLeft,
            })
        case 'UPDATE_BUYING' : 
            return ({
                ...state,
                toggleRight: !state.toggleRight,
                buying: {flag: action.currency.slice(0,2) , currencyCode: action.currency, currency: config[action.currency].name}
            })
        case 'TOGGLE_LEFT' :
            return ({
                ...state,
                toggleLeft: !state.toggleLeft
            })
        case 'TOGGLE_RIGHT' :
            return ({
                ...state,
                toggleRight: !state.toggleRight
            })
        case 'UPDATE_RATE' :
            return ({
                ...state,
                rate: Object.values(action.rate)[0]
            })
        case 'UPDATE_LEFT_INPUT' :
            let selling = (action.val*state.rate).toString().match(/(\d*\.*\d{0,2})/m)[0]
            return ({
                ...state,
                left: action.val,
                right: Number(selling)
            })
        case 'UPDATE_RIGHT_INPUT' :
            let buying = (action.val/state.rate).toString().match(/(\d*\.*\d{0,2})/m)[0]
            return ({
                ...state,
                left: Number(buying),
                right: action.val
            })
        case 'TOGGLE_ALL' :
            return ({
                ...state,
                toggleRight: !state.toggleRight,
                toggleLeft: !state.toggleLeft
            })
        default:
            return state

    }
}