import React, {useState, useEffect, useRef, useReducer} from 'react';
import styled from 'styled-components';
import ListItem from './listItem';
import axios from 'axios';
import {MdSwapHoriz, MdExpandMore} from 'react-icons/md';
import ReactCountryFlag from 'react-country-flag';
import Spinner from './spinner';
import currencyToSymbolMap from 'currency-symbol-map/map'
import CurrencyCalculator from './calculator';
import {useDetectClick} from '../hooks/detectClick';
import {reducer} from '../reducers/RateReducer';
import {SelectWrapper,SelectContainer,SwapButton, InputContainer, DropDownStyles} from './styles.js';

const DropDownContainer = styled.div`${DropDownStyles}`;
const flagStyle = {width: `50px`, height: `100%`}

function Select(props) {
    const initialState = {
        rate: props.initialRate,
        selling: { flag: 'GB', currencyCode: `GBP`, currency: `British Pounds`},
        buying: {flag: 'EU',currencyCode: `EUR`, currency: `Euro`},
        rotate: false,
        toggleLeft: false,
        toggleRight: false,
        left: 1,
        right: props.initialRate
    }

    const [state, dispatch] = useReducer(reducer, initialState);
    // const {ref} = useDetectClick(state)

    useEffect(() => {
        const fetchData = async () => {
            let a = state.selling.currencyCode;
            let b = state.buying.currencyCode
            const result = await axios(
                `https://api.exchangeratesapi.io/latest?base=${a}&symbols=${b}`
            )
            dispatch({type: 'UPDATE_RATE', rate: result.data.rates})
        } 
        fetchData();
    },[state.selling,state.buying])

    useEffect(() => {
        dispatch({type: 'UPDATE_LEFT_INPUT', val: state.left})
    }, [state.rate])
         
    return (
         <SelectWrapper>
            <SelectContainer>
                <ReactCountryFlag style={flagStyle} svg countryCode={state.selling.flag}/>
                <div className='currency-tags'>
                    <p>{state.selling.currencyCode}</p>
                    <p>{state.selling.currency}</p>
                </div>
                <MdExpandMore 
                    style={{transform: state.toggleLeft ? `rotate(180deg)` : `rotate(0deg)`, height: `60px`}} 
                    onClick={() => dispatch({type: 'TOGGLE_LEFT'})}
                    />
                 <DropDownContainer toggle={state.toggleLeft}>
                     {props.rateData.map( e => (
                         <ListItem 
                            key={Math.random()}
                            clicked={() => dispatch({type:`UPDATE_SELLING`, currency: e})}
                            code={e}
                         />
                     ))}
                </DropDownContainer>                
            </SelectContainer>
            <SwapButton>
                <MdSwapHoriz style={{transform: state.rotate ? `rotate(180deg)` : `rotate(0deg)`}} 
                    onClick={() => dispatch({ type: 'SWAP'})}
                />
            </SwapButton>   
            <SelectContainer>
                <ReactCountryFlag style={flagStyle} className='flag'svg countryCode={state.buying.flag}/>
                <div className='currency-tags'>
                    <p>{state.buying.currencyCode}</p>
                    <p>{state.buying.currency}</p>
                </div>
                <MdExpandMore 
                    style={{transform: state.toggleRight ? `rotate(180deg)` : `rotate(0deg)`, height: `60px`}} 
                    onClick={() => dispatch({type: 'TOGGLE_RIGHT'})}
                    />
                <DropDownContainer toggle={state.toggleRight}>
                    {props.rateData.map( e => (
                        <ListItem 
                            // ref={arrowRef} 
                            key={Math.random()*10}
                            clicked={() => dispatch({type:`UPDATE_BUYING`, currency: e})}
                            code={e}
                        />
                    ))}
                </DropDownContainer>
            </SelectContainer>
            {state.rate === undefined ? <><Spinner/><Spinner/></> : 
                <>
                <InputContainer>
                   <span>{currencyToSymbolMap[state.selling.currencyCode]}</span>
                   <input  type="text"  value={state.left} onChange={(e) => dispatch({type: 'UPDATE_LEFT_INPUT', val: e.target.value})}/>
                </InputContainer>
                <InputContainer>
                   <span>{currencyToSymbolMap[state.buying.currencyCode]}</span>
                   <input  type="text"  value={state.right} onChange={(e) => dispatch({type: 'UPDATE_RIGHT_INPUT', val: e.target.value})}/>
                </InputContainer>
                </>
            }
        <CurrencyCalculator symbol={currencyToSymbolMap[state.buying.currencyCode]} rate={state.rate} right={state.right}/>
        </SelectWrapper>
    )
}

export default Select;